import { Layout, Menu, type MenuProps } from 'antd';
import {
  AppstoreAddOutlined,
  BookOutlined,
  CreditCardOutlined,
  DashboardOutlined,
  QuestionCircleOutlined,
  SettingOutlined,
} from '@ant-design/icons';
import {
  type CSSProperties,
  type PropsWithChildren,
  useLayoutEffect,
  useState,
} from 'react';
import { useNavigate, useLocation } from 'react-router';
import logoUrl from '../../assets/logo-white.svg';

const { Sider } = Layout;

type MenuItem = Required<MenuProps>['items'][number];

const items: MenuItem[] = [
  {
    key: '/',
    label: 'Dashboard',
    icon: <DashboardOutlined />,
  },
  {
    key: '/applications',
    label: 'Applications',
    icon: <AppstoreAddOutlined />,
  },
  {
    type: 'divider',
  },
  {
    key: '/documentation',
    label: 'Documentation',
    icon: <BookOutlined />,
    children: [
      { key: '/documentation/install', label: 'Installation' },
      { key: '/documentation/setup', label: 'Setup new app' },
      { key: '/documentation/migration', label: 'Migration from App center' },
      { key: '/documentation/release', label: 'Making release' },
    ],
  },
  {
    key: '/help',
    label: 'Help',
    icon: <QuestionCircleOutlined />,
  },
  {
    type: 'divider',
  },
  {
    key: '/billing',
    label: 'Billing',
    icon: <CreditCardOutlined />,
  },
  {
    key: '/settings',
    label: 'Settings',
    icon: <SettingOutlined />,
  },
];

const SideBar = ({ children }: PropsWithChildren) => {
  const [selectedPath, setPath] = useState<string>('');
  const navigate = useNavigate();
  const location = useLocation();
  const [selectedPathArr, setPathArr] = useState<string[]>([]);
  const [openKeys, setOpenKeys] = useState<string[]>([]);

  useLayoutEffect(() => {
    if (location.pathname !== selectedPath) {
      let path = location.pathname;
      if (location.pathname.includes('applications')) {
        path = '/applications';
      }

      const pathSegments = path.split('/').filter((segment) => segment);
      if (pathSegments.length > 1) {
        const parentKey = `/${pathSegments[0]}`;
        setOpenKeys([parentKey]);
      } else {
        setOpenKeys([]);
      }

      setPath(path);
      setPathArr([path]);
    }
  }, [location, selectedPath]);

  const onMenuClick = ({ key }: { key: string }) => {
    navigate(`${key}`);
  };

  const onOpenChange: MenuProps['onOpenChange'] = (keys) => {
    setOpenKeys(keys as string[]);
  };

  return (
    <Layout hasSider>
      <Sider style={siderStyle}>
        <div
          style={{
            display: 'flex',
            justifyContent: 'center',
            paddingTop: '20px',
          }}
        >
          <img src={logoUrl} style={{ width: 150 }} />
        </div>
        <Menu
          style={{ marginTop: 30 }}
          theme="dark"
          mode="inline"
          selectedKeys={selectedPathArr}
          openKeys={openKeys}
          items={items}
          onOpenChange={onOpenChange}
          onClick={onMenuClick}
        />
      </Sider>
      <Layout style={{ marginInlineStart: 250 }}>{children}</Layout>
    </Layout>
  );
};

const siderStyle: CSSProperties = {
  overflow: 'auto',
  height: '100vh',
  position: 'fixed',
  insetInlineStart: 0,
  top: 0,
  bottom: 0,
  scrollbarWidth: 'thin',
  scrollbarGutter: 'stable',
};

export default SideBar;
